an de sustituir el suelo y proporcionar el anclaje de la planta por medio del sistema radicular. ")]),_c('p',[_vm._v("Adicionalmente, según Robles, S. Y., Tovar, E. C., Guacaneme, C. M., Medina, M. & Cerón, M. F. (2022) se conocen diversas formas de ordenar las plantas, entre las que se encuentran el sistema rectangular, donde la separación es mayor entre surcos (distancia entre filas) que la distancia entre árboles; el cuadro, también conocido como marco real, que es el trazado más simple e implementado y que consiste en sembrar a la misma distancia entre planta y surco.")]),_c('p',{staticClass:"mb-0"},[_vm._v("En el tresbolillo o hexagonal la siembra se realiza en forma de triángulo equilátero, es decir, la misma distancia para todos los lados, con la ventaja de que es posible sembrar mayor cantidad de plantas. Finalmente, el cinco oros o quincunce es similar al cuadrado, pero en la parte central del cuadrado se siembra una planta. ")])]),_c('div',{staticClass:"col-lg-6 col-10"},[_c('div',{staticClass:"BGR07 p-4"},[_c('div',{staticClass:"titulo-sexto color-acento-contenido mb-4"},[_c('h5',[_vm._v("Figura 5")]),_c('span',[_vm._v("Tipos de trazado")])]),_c('div',{staticClass:"row justify-content-around mb-4"},[_c('div',{staticClass:"col-lg-4 col-6"},[_c('div',{staticClass:"h4 text-center"},[_vm._v("Cuadrado o marco")]),_c('figure',[_c('img',{staticClass:"w-80",attrs:{"src":require("@/assets/curso/tema3/img05.png")}})])]),_c('div',{staticClass:"col-lg-4 col-6"},[_c('div',{staticClass:"h4 text-center"},[_vm._v("Rectangular")]),_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema3/img06.png")}})])])]),_c('div',{staticClass:"row justify-content-around"},[_c('div',{staticClass:"col-lg-4 col-6"},[_c('div',{staticClass:"h4 text-center"},[_vm._v("Tresbolillo")]),_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema3/img07.png")}})])]),_c('div',{staticClass:"col-lg-4 col-6"},[_c('div',{staticClass:"h4 text-center"},[_vm._v("Cinco oros o quincunce")]),_