as, equipos, elementos y acciones como:")]),_c('div',{staticClass:"row justify-content-center mb-4"},[_vm._m(11),_c('div',{staticClass:"col-lg-7",attrs:{"data-aos":"fade-left"}},[_c('AcordionA',{staticClass:"mb-5",attrs:{"tipo":"a","clase-tarjeta":"BGR08"}},[_c('div',{attrs:{"titulo":"Procesos y tecnologías que permiten la planeación"}},[_c('p',{staticClass:"mb-0"},[_vm._v("Día a día se incluyen procesos y tecnologías que permiten la planeación; esto implica la determinación de lo que se quiere realizar, incluyendo las decisiones de gran importancia como el establecimiento de objetivos, programas, definición de técnicas, métodos, procedimientos y demás prioridades dentro del proceso de siembra. Es importante identificar entonces, las acciones a desarrollar a través de una secuencia lógica y ordenada que permita obtener resultados exitosos.")])]),_c('div',{attrs:{"titulo":"Registro organizado de las actividades desarrolladas"}},[_c('p',{staticClass:"mb-0"},[_vm._v("Se requiere que el productor lleve un registro organizado de las actividades desarrolladas, por eso es indispensable el diseño de los formatos que permitan plasmar la información recolectada, que debe estar disponible en físico y sistematizada para tecnificar el proceso y evitar retrasos en la ejecución de las actividades que generen un desequilibrio y pérdidas productivas. Resulta indispensable planear perfectamente el proceso, reducir los contratiempos y lograr que el proceso se realice de manera eficiente y eficaz.")])]),_c('div',{attrs:{"titulo":"Diligenciar formatos"}},[_c('p',[_vm._v("El diligenciamiento de estos formatos va enlazado con el cronograma que permite establecer una guía de las temporadas del año y las condiciones de siembra en cada una, con el fin de establecer un alto porcentaje de crecimiento y desarrollo del cultivo. Se debe tener en cuenta:")]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-6"},[_c('ul',{staticClass:"lista-ul--color"},[_c('li',[_c('i'